<template>
  <div class="milkfarm" v-if="isLoaded">
    <div class="cards-wrapper drawn-cards">
      <div class="cards" :class="{'tight': game.state.drawnCards.size() > 8}">
        <template v-for="card in game.drawnCards.cards" v-bind:key="card">
          <PlayingCard :card="card" :class="{'scaled': card.scale}" @click="toggleScale(card); game.clickCard(card);"></PlayingCard>
        </template>
      </div>
    </div>
    <DialogueCard :menu="menu"></DialogueCard>
    <div class="cards-wrapper picked-cards">
      <div class="cards" :class="{'tight': game.state.pickedCards.size() > 5}">
        <template v-for="card in game.state.pickedCards.cards" v-bind:key="card">
          <PlayingCard :card="card" :class="{'scaled': card.scale}" @click="toggleScale(card)"></PlayingCard>
        </template>
      </div>
    </div>
    <div class="dices">
      <template v-for="die in game.state.dice" v-bind:key="die">
        <DiceView :dice="die"></DiceView>
      </template>
      <div class="dice-view add-dice" v-if="game.allowDiceBuy()" @click="game.buyDice()">
        <p>+</p>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { Options, Vue } from 'vue-class-component'
import Game from '@/Game'
import ValueMeter from '@/components/ValueMeter.vue'
import TextButton from '@/components/TextButton.vue'
import globalState from '@/store/globalState'
import SaveSystem from '@/SaveSystem'
import PlayingCard from "@/components/PlayingCard.vue"
import Card from '@/Card'
import DiceView from "@/components/DiceView.vue"
import PatreonButton from "@/components/PatreonButton.vue"
import Menu from "@/Menu"
import DialogueCard from "@/components/DialogueCard.vue"

@Options({
  components: {
    TextButton, PlayingCard, DiceView, DialogueCard
  },
  props: {
    menu: Object
  },
  methods: {
    log(msg: any) {
      console.log(msg)
    },
    toggleScale(card: Card) {
      card.scale = !card.scale
      if (this.scaledCard == card) {
        this.scaledCard.scale = false
        this.scaledCard = new Card({})
      } else {
        this.scaledCard.scale = false
      }
      if (card.scale) this.scaledCard = card
    }
  },
  computed: {
    game: function (): Game {
      return this.menu.game
    }
  },
  async created() {
    //await globalState().load()
    this.isLoaded = true
  }
})
export default class GameView extends Vue {
  isLoaded = false
  //saveSystem!: SaveSystem
  //farm!: Farm
  _game!: Game
  menu!: Menu
  scaledCard: Card = new Card({})
}
</script>

