import Game from "@/Game"
import globalState from "@/store/globalState"
import { reactive } from "vue"
import Deck from "@/Deck"
import axios from "axios"

export default class Menu {
  public static readonly GAME_VERSION = '0.5.1'

  private _activeComponent: string = "StartMenu"
  private _game: any = undefined

  constructor() {
    globalState().load()
  }

  get game() {
    return this._game
  }

  startGame() {
    this._game = reactive(new Game(this))
    this.activeComponent = this.game!.activeComponent
  }

  endGame() {
    this._game = undefined
    this.activeComponent = "StartMenu"
  }

  get activeComponent(): string {
    if (this.game != undefined) return this.game.activeComponent
    return this._activeComponent
  }

  set activeComponent(value: string) {
    this._activeComponent = value
  }

  artCredits(): any[] {
    let artists: any = JSON.parse(globalState().artistsString.value)
    for (let artist of artists) {
      artist.cards = []
    }
    let deck = new Deck()
    for (let card of deck.allLevels) {
      let a = artists.find((a: any) => a.artist == card.artist)
      if (a == undefined) continue

      if (card.isParent) a.cards.push(`${card.name}`)
      else if (card.hasParent) a.cards.push(`${card.parent.name}: ${card.name}`)
      else a.cards.push(`${card.name}`)
    }
    for (let artist of artists) {
      artist.cards.sort()
    }
    return artists
  }

  patreonCredits() {
    let credits = globalState().patreonCredits.value
    return credits
  }

  get version(): string {
    return Menu.GAME_VERSION
  }
}
